import React from "react";
import { useHistory } from "react-router-dom";
import Avatar from "@material-ui/core/Avatar";

const PostPage = ({ posts, themeIsLight }) => {
  const history = useHistory();
  const path = +history.location.pathname.substring(6);
  const { author, avatar, content, date, image, tags, title } = posts.find(
    (post) => post.id === path
  );
  return (
    <div className='page postPage'>
      <div className='postPage__image'>
        <img src={`${image}`} alt='post screenshot' />
      </div>
      <div className='postPage__title'>
        <h3>{title}</h3>
        <p>{date}</p>
      </div>
      <div className='postPage__author'>
        <Avatar alt='user avatar' src={avatar} />
        <p>{author}</p>
      </div>
      <div className='postPage__content'>
        <p>{content}</p>
      </div>
      <div className='postPage__tags'>
        {tags.map((tag) => (
          <p
            key={tag}
            className={`postPage__tag ${
              themeIsLight ? "" : "postPage__tag--isDark"
            }`}
          >
            {tag}
          </p>
        ))}
      </div>
    </div>
  );
};

export default PostPage;
